@font-face {
  font-family: 'TmoneyRoundWindRegular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/TmoneyRoundWindRegular.woff')
    format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'TmoneyRoundWindExtraBold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/TmoneyRoundWindExtraBold.woff')
    format('woff');
  font-weight: normal;
  font-style: normal;
}

#root {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
  -ms-user-select: none;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  letter-spacing: 0.05em;
}

body {
  background: #181d27;
  font-family: 'TmoneyRoundWindRegular';
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.font-bold {
  font-family: 'TmoneyRoundWindExtraBold';
  font-weight: lighter;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #181d27;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.highlight-yellow {
  background: linear-gradient(to top, #ffe400 100%, transparent 100%);
}

.highlight-white {
  background: linear-gradient(to top, white 100%, transparent 100%);
}

.highlight-red {
  background: linear-gradient(to top, #dc143c 100%, transparent 100%);
}

.highlight-lapis {
  background: linear-gradient(to top, #5977d3 100%, transparent 100%);
}

.text-white {
  color: white;
}

.text-black {
  color: #181d27;
}
